@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --clr-dark-1: #1d3557;
  --clr-dark-2: #457b9d;
  --clr-light-1: #a8dadc;
  --clr-light-2: #f1faee;
  --clr-accent: #e63946;

  --bs-deep: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  --bs-subtle: 0 1px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
  --bs-hover: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 250ms ease-out;
}

body {
  font-family: 'Roboto'; 
  background-color: #1d3557; 
  background-color: var(--clr-dark-1);
  width: 100vw;
  overflow-x: hidden;
} 

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins';
  font-weight: 500;
}

.btn-link {
  text-align: center;
  margin-left: 50%;
  transform: translate(-50%);
  display: block;
  margin-top: 3em;
  margin-bottom: 3em;
  color: #f1faee;
  color: var(--clr-light-2);
  background: #ee0979;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ff6a00, #ee0979); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 1em;
  border-radius: 3em;
  font-size: 1.1em;
  max-width: 250px;
  transition: all 1s ease-out;
}

.btn-link .btn-link:focus, .btn-link:hover, .btn-link:visited, .btn-link:link, .btn-link:active {
  text-decoration: none;
}



.btn-link::before {
    border-radius: inherit;
    background: #ee0979;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #ff6a00, #ee0979); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    content: '';    
    display: block;
    height: 100%;
    position: absolute;
    top: 0; left: 0;
    opacity: 0;
    width: 100%;
    z-index: -100;
  }


  .btn-link:hover::before{
    opacity: 1;
  }
path[fill='#123456']{display:none !important;}
path{
    cursor:pointer;
    stroke:rgba(100, 100, 100, 0.301);
}

.chart-container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    overflow: hidden !important;
    justify-content:center;
    align-items: center;
    overflow: scroll;
    background: var(--clr-light-2);
    padding: 2em 0;
    border-bottom-left-radius: 38px;
    border-bottom-right-radius: 38px;
    box-shadow: var(--bs-subtle);
}

.chart-heading {
    width: 100vw;
    text-align: center;
    color: var(--clr-dark-1);
}

.table-container {
    background-color: var(--clr-dark-1);
}

.loader {
    color: var(--clr-light-2);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.cards-container {
    width: 100%;
    display: flex;
    padding: 2em;
    background-color: var(--clr-dark-1);
    flex-wrap:wrap;
    justify-content: space-between;
}

.text-info {
    font-size: 1rem;
    margin-top: 2em;
    width: 100vw;
    text-align: center;
    color: var(--clr-dark-1);
    font-weight: 700;
    font-family: "Poppins";
}

.text-sorry {
    width: 10vw;
    font-size: 0.8rem;
    color: var(--clr-dark-1);
}

.text-sorry i {
    color: var(--clr-accent);
}

.table-container {
    border: 0.5em solid transparent;
    border-radius: 0.5em;
    padding:0 0.5em;
    width: 100%;
    overflow-x:scroll;
    margin: 10px 0;
    scrollbar-width: none;
    -ms-overflow-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.table-container::-webkit-scrollbar {
    width: 0px;
    margin-top: 2em;
    background: transparent;
}

.text-table-info {
    display: none;
    font-size: 0.9em;
    margin: 1em;
    color: var(--clr-light-2);
    text-align:center;
}

@media(max-width: 600px) {

    .text-sorry {
        width: 90vw;
        margin: 5px auto;
    }

    .text-table-info {
        display: block;
    }
}

.centered-link {
    text-align: center;
}
.card {
    flex: 1 1 20%;
    min-height: 90px;
    border-radius: 1em;
    box-shadow: var(--bs-subtle);
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 0 1em;
    margin: 1em !important;
    color: #252627;
}

.card-info {
    padding: 1em;
    height: 100%;
    margin: 0 auto;
}

.card-info p {
    font-family: 'Poppins';
    font-size: 2.3em;
    font-weight: 900;
}

.card-blue {
    background-color: #4facfe;
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}

.card-red {
    background-color: #fee140;
    background-image: linear-gradient(to right, #fa709a 0%, #fee140 100%);
}

.card-green {
    background-color: #43e97b;
    background-image: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);
}

.card-grey {
    background-color: #f8f9d2;
    background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

@media(max-width: 1050px) {
    .card{
        width: 100%;
        margin: 10px 0 !important;
    }
}

.table {
    display: inline-block;
    margin: 0 auto;
    text-align: left;
    border-collapse: collapse;
    box-shadow: var(--bs-deep);
    border-radius: 1em;
    padding: 0.5em;
    background: var(--clr-light-2);
    overflow-x: scroll;
}

.table thead {
    background: var(--clr-light-2);
    font-family: "Poppins";
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    text-align: left;
} 

.table thead th{
    padding: 1em;
}

.table thead tr, .table tbody tr {
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.table tbody td {
    padding: 1em;
}

.table tbody {
    font-family: "Roboto";
    color: var(--clr-dark-1);
    text-align: left;
}

.table tbody tr {
    background: var(--clr-light-2);
    font-weight: 600;
}
.map {
    color: var(--clr-light-2);
    height: auto;
}

.legend {
    width: 80vw;
    display: flex;
    justify-content: space-evenly;
    margin-left: 50%;
    transform: translate(-50%);
    color: var(--clr-dark-1);
}

.legend-item-holder {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.legend-item-holder span {
    padding: 5px;
}

.legend-item {
    width: 20px;
    height: 20px;
}

@media(max-width: 800px){
    .legend-item-holder {
    flex-direction:column;
    align-items:center;
    font-size: 0.6em;
}


.legend-item {
    width: 10px;
    height: 10px;
}
}
.news {
    padding: 0.5em 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.news-card {
    width: 50%;
    flex-grow: 1;
    flex-shrink: 1;
    background: var(--clr-light-2);
    padding: 1em;
    margin-bottom: 1.5em;
    border-radius: 0.5em;
}

.news-img {
    width: 100%;
    border-radius: 0.5em;
    max-height: 300px;
    min-width: 300px;
}

.news-card-title {
    margin-bottom: 1em;
    margin-top: 0.5em;
}

.news-card-text {
    line-height: 1.6em;
    font-size: 1.15em;
}

.verification-text {
    text-align:center;
    padding: 3em;
    font-size: 1.1em;
    font-family: "Poppins";
    color: var(--clr-light-1);
}

.verification-text i {
    color: #25d366;
    font-size: 1.5em
}

@media(max-width: 800px) {
    .news-card {
        width: 100%;
        border-radius: 0;
    }
}

@media(min-width: 801px) {

    .news-card {
        width: 70vw;
        display: flex;
        justify-content: space-between;
    }

    .news-img {
        width: 40%;
    }

    .news-card-body {
        padding-left: 1em;
    }

    .verification-text {
        font-size: 2em;
        padding: 2em;
    }

}
.footer {
    background-color: rgba(255, 255, 255, 0.1);
    padding: 2em;
    text-align: center;
    color: var(--clr-light-2);
}

.footer .copyrights {
    margin-top: 1em;
    opacity: 0.6;
}

.footer-link {
    text-decoration: none;
    font-size: 1.1em;
    color: var(--clr-light-2); 
    line-height: 1.5em;
    position: relative;
    letter-spacing: 0.05em;
    font-family: "Poppins";
}

.footer-link::after{
    content: "";
    width: 100%;
    position: absolute;
    bottom: 2px;
    left: 0;
    right: 0;
    height: 4px;
    transition: 250ms linear;
    opacity: 0.5;
    background: #ee0979; 
    background: linear-gradient(to left, #ff6a00, #ee0979); 
}

.footer-link:hover::after {
    height: 2px;
    bottom: 0;
    opacity: 1;
}

